.video-container {
  position: relative;
  z-index: 3;
  margin-top: -80px;
  padding: 170px 0 100px;
  background-color: #050039;

  @include breakpoint(tablet) {
    padding: 170px 0;
  }

  .video-inner {
    display: flex;
    flex-direction: column;
  }

  h2 {
    width: 90%;
    margin-bottom: 60px;

    @include breakpoint(tablet) {
      width: 60%;
    }
  }

  

  .video-placeholder {    
    display: none;
    align-self: center;    

    @include breakpoint(tablet) {
      display: inline-block;
    }

    &--mobile {
      display: inline-block;
      text-align: center;

      @include breakpoint(tablet) {
        display: none;
      }
    }
  }    
}
  
.discover {
  position: relative;
  z-index: 2;
  padding: 80px 0 100px;
  background-color: #000022;

  @include breakpoint(tablet) {
    padding: 130px 0 150px;
  }

  h2 {
    width: 90%;

    @include breakpoint(tablet) {
      width: 60%;
    }
  }
}

.checkers {
  position: relative;
  z-index: 2;
  display: block;
  width: 100%;
  height: 438px;
  background-image: url('../images/backgrounds/checkers-bg.png');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

.signup {
  position: relative;
  z-index: 2;
  background-color: #030066;
  text-align: center;

  @include breakpoint(tablet) {
    padding: 140px 0;
  }
}

.signup-box {
  position: relative;
  display: inline-block;
  width: 100%;

  @include breakpoint(tablet) {
    max-width: 720px;
  }

  img {
    width: 100%;
  }

  &:before {
    content: '';
    position: absolute;
    top: 247px;
    left: 50%;
    margin-left: -63px;
    display: none;
    width: 126px;
    height: 126px;
    background-image: url('../images/signup-bolt.png');

    @include breakpoint(tablet) {
      display: inline-block;
      top: 327px;
    }
  }

  &__inner {
    padding: 60px;
    background-color: #030066;
    border-radius: 0 0 12px 12px;

    @include breakpoint(tablet) {
      background-color: #000022;
    }

    h2 {
      margin-top: 0;
      margin-bottom: 25px;
      font-size: 42px;
      line-height: 48px;
      letter-spacing: -.3px;
      background: -webkit-linear-gradient(180deg, #FFA1FF 0%, #D905F0 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }

    p {
      width: 75%;
      margin: auto;

      @include breakpoint(tablet) {
        width: 100%;
      }
    }

    .range {
      display: flex;
      flex-direction: column;
      margin-top: 30px;
      padding:10px 25px;
      background-color: #010042;
      border-radius: 10px;
      align-items: center;
      gap: 10px;
      justify-content: space-between;

      @include breakpoint(tablet) {
        flex-direction: row;
        border-radius: 30px;
        gap: 20px;
        padding:6px 25px;
      }

      span {
        font-size: 24px;
        font-weight: 500;
      }

      &__bar {
        position: relative;
        height: 10px;
        flex-grow: 1;
        width: 100%;
        border-radius: 30px;
        background-color: #030066;

        @include breakpoint(tablet) {
          width: auto;
        }

        &:before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          display: inline-block;
          width: 60px;
          height: 10px;
          border-radius: 30px;
          background-image: $light-grad;
        }
      }

      small {
        font-size: 14px;
        color: #4022F3;
      }
    }

    .email-signup {
      display: flex;
      margin-top: 40px;
      justify-content: space-between;
      flex-direction: column;
      gap: 15px;

      @include breakpoint(tablet) {
        flex-direction: row;
        gap: 30px;
      }

      input {
        flex-grow: 1;
        background-color: #010042;
        border: 0;
        border-radius: 10px;
        padding: 17px 20px 15px;
        color: #fff;

        &:focus {
          border: 1px solid #4022F3;
        }        

        &::placeholder {
          color: #9684FF;
          font-style: italic;
        }
      }

      .btn {
        line-height: 38px;
      }
    }
  }
}